<template>
  <div class="container home-content">
    <div class="row">
      <div class="col-sm-7 col-lg-6">
        <div class="home-left-content">
          <h1>Calculate Your ROI</h1>
          <p>How would Midigator’s outcomes compare to your current chargeback management efforts? Enter a few numbers into our ROI calculator and see for yourself!</p>
          <router-link to="/product"><button class="button-main start-button" @click="start">START</button></router-link>
        </div>
      </div>
      <div class="col-sm-5 col-lg-6">
        <div class="home-right-content">
          <img src="@/assets/images/calculate.png" class="calculator-img">
        </div>
      </div>
    </div> <!-- .row END -->
  </div> <!-- .container END -->
</template>

<script>
import Products from '@/components/Product'
import { mapState, mapMutations, mapActions } from 'vuex'

export default {
  name: 'Home',
  components: {
    Products
  },
  data () {
    return {
      newLink: ''
    }
  },
  computed: {
    ...mapState([
      'title',
      'links'
    ])
  },
  methods: {
    ...mapMutations([
      'ADD_LINK'
    ]),
    ...mapActions([
      'removeLink'
    ]),
    addLink: function () {
      this.ADD_LINK(this.newLink)
      this.newLink = ''
      // console.log('state.links after add: ', this.$store.state.links)
    },
    removeLinks: function (link) {
      this.removeLink(link)
    },
    test: function () {
      return console.log('state.links onload: ', this.$store.state.links)
    },
    start: function () {
      console.log('hello mother! hello father! chchchchc cherry bomb!')
    }
  },
  beforeMount: function () {
    // this.test()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .home-left-content {
    padding-top: 45px;
    text-align: center;

    p {
      font-weight: 600;
      font-size: 18px;
      line-height: 1.5em;
      margin-bottom: 45px;
    }
  }

  .calculator-img {
      width: 510px;
      margin-top: 50px;
  }

  @media(min-with: 480px) {
    .home-left-content {
      p {
        font-size: 20px;
      }
    }
  }

  @media(min-width: 768px) {

    .home-left-content {
      padding-top: 150px;
      padding-right: 50px;
    }

    .calculator-img {
      float: right;
      margin-top: 135px;
    }
  }

  @media(min-width: 1200px) {
    .home-left-content {
      padding-top: 200px;
    }
  }

  @media(max-height: 700px) and (min-width: 1200px) {
    .home-left-content {
      padding-top: 95px;
    }

    .calculator-img {
      margin-top: 30px;
    }
  }

</style>



