import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Product from '@/components/Product'
import Industry from '@/components/Industry'
import p404 from '@/components/p404'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/product',
      name: 'Product',
      component: Product
    },
    {
      path: '/industry',
      name: 'Industry',
      component: Industry
    },
    {
      path: '/404',
      name: 'NotFound',
      component: p404
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})



