<template>
  <div class="previous-container container">
    <div class="row">
      <div class="col-sm-12">
        <button @click="goBack" class="previous-link">
          <i class="material-icons back-arrow">arrow_back</i>Previous
        </button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Previous',
  data () {
    return {
    }
  },
  methods: {
    goBack () {
      this.$router.back()
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

  @import '@/assets/scss/variables.scss';

  .previous-link {
    font-size: 20px;
    color: $primary-color;
    background-color: transparent;

    &:hover,
    &:focus {
      outline: none;
      text-decoration: underline;
    }
  }

  .back-arrow {
    font-weight: bold;
    padding-right: 10px;
    vertical-align: middle;
    margin-bottom: 3px;
  }

  @media(min-width: 480px) {
  }

  @media(min-width: 768px) {
  }

  @media(min-width: 1200px) {
  }

  @media(max-height: 700px) and (min-width: 1200px) {
  }

</style>



