<template>
  <div id="app">
    <Header />
    <section class="main-content">
      <Progress v-if="hideOnRoutes" />
      <router-view class="view-content" />
      <Previous v-if="hideOnRoutes" />
    </section>
    <Footer />
  </div>
</template>

<script>
import Progress from '@/components/shared/progress'
import Header from '@/components/shared/header'
import Footer from '@/components/shared/footer'
import Previous from '@/components/shared/previous'

export default {
  name: 'App',
  components: {
    Progress,
    Header,
    Footer,
    Previous
  },
  data () {
    return {
    }
  },
  computed: {
    hideOnRoutes () {
      const routes = ['/', '/home', '/404']
      const check = routes.filter((route) => {
        return this.$route.path === route
      })
      return check.length <= 0
    }
  }
}
</script>

<style lang="scss">
  @import '@/assets/scss/reset.scss';
  @import '@/assets/scss/grid.scss';
  @import '@/assets/scss/helpers.scss';
  @import './assets/scss/variables.scss';

  html {
    background-color: #fff;
    width: 100%;
    height: 100%;
  }

  body {
    font-family: "Open Sans", Arial, Helvetica, sans-serif;
    width: 100%;
    height: 100%;
    color: $text-color;
    background: url('/assets/images/background.png') repeat-y 0 0;
    background-size: 1440px;
  }

  *,
  :after,
  :before {
    box-sizing: border-box;
  }

  #app {
    overflow: hidden;
  }

  h1,
  h2,
  h3 {
    font-family: inherit;
    font-weight: 600;
    color: #4a4a4a;
  }

  h1 {
    font-size: 40px;
    margin-bottom: 50px;
    letter-spacing: -2px;
  }

  h2 {
    text-align: center;
    font-size: 19px;
    margin-bottom: 35px;
  }

  a {
    color: $text-color;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
        cursor: pointer;
    }
  }

  p,
  li {
    color: inherit;
    opacity: 0.8;
  }

  .main-content {
    margin-top: 85px;
    margin-bottom: 80px;
  }

  .button-main {
    width: 149px;
    height: 60px;
    background-color: #AFD25C;
    color: #fff;
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    transition: background-color 0.20s;

    &:hover {
        background-color: #b3e243;
    }
  }

  .view-content {
    margin-bottom: 50px;
  }

  .view-headline {
    text-align: center;
    margin-bottom: 50px;

    h2 {
      display: inline;
      vertical-align: middle;
    }
  }

  /* Media Queries */

  @media(min-width: 480px) {
    .main-content {
      margin-top: 130px;
    }

    h2 {
      font-size: 24px;

      br {
        display: none;
      }
    }
  }

  @media(min-width: 768px) {
    .view-content {
      margin-bottom: 130px;
    }
  }

  @media(min-width: 992px) {
    h1 {
      font-size: 52px;
    }
  }

</style>



