<template>
  <div class="container progress-container">
    <div class="row">
      <div class="col-sm-12">
        <div class="progress-box clearfix">
          <div class="progress-bg">
            <div class="progress-bar"></div>
          </div>
          <div class="progress-percent">10%</div>
        </div>
      </div>
    </div>
  </div> <!-- .container END -->
</template>

<script>
export default {
  name: 'Progress',
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import '@/assets/scss/variables.scss';

  .progress-container {
    margin-top: 45px;
    margin-bottom: 50px;
  }

  .progress-box {
    position: relative;
    width: 87%;
    margin-right: 40px;
  }

  .progress-bg {
    float: left;
    width: 100%;
    height: 13px;
    background-color: #f6f6f6;
    border: 1px solid #e6e6e6;
    border-radius: 10px;
  }

  .progress-bar {
    width: 10%;
    height: 13px;
    background-color: $primary-color;
    border-radius: 10px;
  }

  .progress-percent {
    position: absolute;
    top: 0;
    right: -40px;
    color: $primary-color;
    font-size: 12px;
    padding-top: 2px;
  }

  @media(min-width: 480px) {

    .progress-container {
      margin-bottom: 95px;
    }

    .progress-box {
      width: 350px;
      float: right;
    }

  }

  @media(min-width: 768px) {
  }

  @media(min-width: 1200px) {
  }

  @media(max-height: 700px) and (min-width: 1200px) {
  }

</style>



